#footer {
  position: relative;
  z-index: 4;
  background: #fff;
  color: #333333;
  padding: 0;
  &:after {
    content: "";
    position: absolute;
    z-index: 2;
    top: -1.5rem;
    left: 50%;
    transform: translate(-50%, 0);
    width: 42rem;
    height: 5rem;
    background: #fff;
    clip-path: polygon(4% 0, 96% 0, 100% 30%, 100% 70%, 100% 100%, 0 100%, 0 70%, 0 33%);
    pointer-events: none;
  }
}
.footer-top {
  background: #fff;
  padding: 1.75rem 0 3.125rem;
  .container-fluid {
    z-index: 3;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 3.125rem;
  }
  &__left {
    display: flex;
    align-items: center;
    gap: 1rem;
  }
  &__logo {
    width: 7.5rem;
    flex-shrink: 0;
    img {
      width: 100%;
    }
  }
  &__text {
    max-width: 23.125rem;
    strong {
      font-family: "Arkhip";
      font-size: 1.25rem;
      line-height: 130%;
      text-transform: uppercase;
    }
    p {
      font-size: 1rem;
      line-height: 130%;
      margin: 0.5rem 0 0 0;
    }
  }
  &__right {
    flex-shrink: 0;
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 3.125rem;
  }
  &__wrap {
    display: flex;
    gap: 2.5rem;
  }
  &__wrap:nth-child(1) {
    gap: 4rem;
  }
  &__location {
    display: flex;
    align-items: start;
    gap: 1rem;
    &:hover {
      i {
        background: #00148B;
        svg path {
          stroke: #fff;
        }
      }
    }
    i {
      width: 2.25rem;
      height: 2.25rem;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #fff;
      border-radius: 50%;
      margin-top: 0.75rem;
      svg {
        width: 1.125rem;
        height: auto;
      }
    }
    p {
      font-size: 1rem;
      line-height: 130%;
      margin: 0;
    }
    p strong {
      display: block;
      font-size: 1.125rem;
      font-weight: 700;
      margin-bottom: 0.1875rem;
    }
  }
  &__group {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.75rem;
  }
  &__email,
  &__support,
  &__website {
    display: flex;
    align-items: start;
    gap: 1rem;
    i {
      width: 2rem;
      margin-top: 0.75rem;
    }
    p {
      font-size: 1.125rem;
      line-height: 130%;
      margin: 0;
    }
    strong {
      display: block;
      font-weight: 700;
      margin-bottom: 0.3125rem;
    }
    a {
      border-bottom: 0.0625rem solid transparent;
    }
    a:hover {
      color: #00148B;
      border-color: #00148B;
    }
  }
  &__btn {
    display: flex;
    align-items: start;
    background: transparent;
    border-bottom: 0.0625rem solid #00148b;
    font-size: 0.875rem;
    line-height: 130%;
    color: #00148B;
    margin-left: 3.5rem;
    &:hover {
      border-color: #8FA8CC;
      color: #8FA8CC;
    }
    span {
      min-width: unset;
      padding: 0;
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 0.75rem;
  }
  &__social {
    width: 2.5rem;
    &:hover {
      opacity: 0.7;
    }
    img {
      width: 100%;
    }
  }
  &__phone {
    display: block;
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 700;
    &:hover {
      color: #00148B;
    }
  }
  &__btn2 {
    display: block;
    background: transparent;
    margin-top: 0.5rem;
    &:hover {
      color: #00148b;
      span {
        border-color: #00148b;
      }
    }
    span {
      min-width: unset;
      border-bottom: 0.0625rem dashed #333333;
      font-size: 0.875rem;
      line-height: 130%;
      font-weight: 700;
      padding: 0;
      transition: all 0.3s;
    }
  }
}
.footer-bottom {
  background: #000000;
  padding: 1.25rem 0;
  font-size: 0.75rem;
  line-height: 130%;
  color: #fff;
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 1.25rem 3.125rem;
  }
  &__left {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 0.75rem 10rem;
  }
  &__logo {
    width: 8.75rem;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 0.75rem 1rem;
    p {
      margin: 0;
    }
  }
  &__text2 {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 0.75rem 3.125rem;
    a {
      color: #8FA8CC;
      border-bottom: 0.0625rem solid #8FA8CC;
    }
    a:hover {
      border-color: transparent;
    }
    p {
      margin: 0;
    }
  }
}

@media screen and (max-width: 1600px) {
   .footer-top {
     &__inner {
       gap: 2rem;
     }
     &__right {
       gap: 2rem;
     }
     &__wrap {
       gap: 2rem;
     }
     &__wrap:nth-child(1) {
      gap: 3rem;
     }
   }
  .footer-bottom {
    &__left {
      gap: 0.75rem 3.125rem;
    }
  }
}

@media screen and (max-width: 1300px) {
  .footer-top {
    &__logo {
      width: 6.25rem;
    }
    &__text {
      max-width: 20rem;
      strong {
        font-size: 1rem;
      }
      p {
        font-size: 0.875rem;
        margin-top: 0.5rem;
      }
    }
    &__wrap:nth-child(2) {
      flex-direction: column;
      gap: 0.5rem;
    }
    &__socials {
      order: 1;
    }
  }
}

@media screen and (max-width: 1023px) {
  #footer {
    &:after {
      top: -1.25rem;
      width: 24rem;
    }
  }
  .footer-top {
    &__inner {
      flex-direction: column;
    }
    &__right {
      width: 100%;
    }
  }
}

@media screen and (max-width: 768px) {
  #footer {
    &:after {
      clip-path: polygon(8% 0, 92% 0, 100% 30%, 100% 70%, 100% 100%, 0 100%, 0 70%, 0 33%);
    }
  }
  .footer-top {
    padding: 1.25rem 0 1.875rem;
    &__inner {
      gap: 1.25rem;
    }
    &__right {
      flex-direction: column;
      gap: 1.875rem;
    }
    &__wrap:nth-child(1) {
      flex-direction: column;
      gap: 0.75rem;
    }
    &__wrap:nth-child(2) {
      flex-direction: row;
      gap: 2rem;
    }
    &__socials {
      order: -1;
    }
    &__location {
      i {
        width: 2rem;
      }
    }
  }
  .footer-bottom {
    padding: 0;
    &__inner {
      position: relative;
      flex-direction: column;
      align-items: start;
      padding: 1.25rem 0 3.75rem;
    }
    &__left {
      flex-direction: column;
      align-items: start;
    }
    &__logo {
      position: absolute;
      left: 0;
      bottom: 1.25rem;
    }
    &__text,
    &__text2 {
      flex-direction: column;
      align-items: start;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer-top {
    &__left {
      gap: 0.75rem;
    }
    &__logo {
      width: 3rem;
    }
    &__text {
      p {
        font-size: 0.75rem;
      }
      strong {
        font-size: 0.875rem;
      }
    }
    &__location {
      p {
        font-size: 0.875rem;
      }
    }
    &__email,
    &__support,
    &__website {
      p {
        font-size: 0.875rem;
      }
    }
    &__phone {
      font-size: 1rem;
    }
  }
}

@media screen and (max-width: 359px) {
  #footer {
    &:after {
      width: 16rem;
    }
  }
}